@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Readex+Pro&family=Roboto:wght@400;700&display=swap);
*{box-sizing:border-box}body,h1,li,ul{margin:0}li{list-style:none}html{font-family:"Readex Pro", sans-serif}body{background-color:#626367}.App{display:flex;justify-content:center;align-items:center}.App__container{display:flex;justify-content:space-between;align-items:center;margin:80px auto;padding:20px;width:600px;background-color:#fff;border-radius:10px;box-shadow:0 10px 10px rgba(0,0,0,0.1)}.App__list{display:flex;flex-direction:column;padding:10px}.App__buttons{display:flex;flex-direction:column}.App__button{padding:8px;margin:10px;border-radius:10px;border:1px solid transparent;font-weight:bold;background-color:#fff;color:#0d6b6f;box-shadow:0 2px 2px 1px rgba(0,0,0,0.1);font-family:inherit;cursor:pointer;transition:background-color 0.3s, color 0.3s}.App__button--go{margin:200px auto}.App__button:not(:last-child):hover{background-color:#839d83;color:#fff}.App__button:last-child:hover{background-color:#cf3f34;color:#fff}

